html,
body {
  height: 100%;
}

body {
  color: #000000;
  font-weight: 300;
  font-family: "Open Sans", Arial, Tahoma, sans-serif;
  font-size: 1.125rem;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.button {
  position: relative;
  z-index: 1;
  border: none;
  color: #ffffff;
  font-family: "Days One", Arial, Tahoma, sans-serif;
  cursor: pointer;
  transition: all .2s linear;
  border-radius: 3px;
  font-size: 1rem;
  text-shadow: 1px 1px 1px #000000;
}

.button--small {
  padding: 7px 26px 9px 26px;
}

.button--small-white {
  padding: 7px 26px 9px 26px;
}

.button--small-white::after {
  border: 1px solid #000000;
  background: #ffffff;
  opacity: 0;
  transition: all .2s linear;
}

.button--small-white:hover {
  color: #000000;
  text-shadow: 1px 1px 1px #ffffff;
}

.button--small-white:hover::after {
  opacity: 1;
  transition: all .2s linear;
}

.button--big {
  text-transform: uppercase;
  padding: 22px 32px;
}

.button::before, .button::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  content: '';
}

.button::before {
  border: 1px solid #000000;
  background: linear-gradient(#313132, #000000);
}

.button::after {
  border: 1px solid #be9943;
  background: linear-gradient(#d4aa49, #be9943);
  opacity: 0;
  transition: all .2s linear;
}

.button:hover {
  text-shadow: 1px 1px 1px #a0843e;
}

.button:hover::after {
  opacity: 1;
  transition: all .2s linear;
}

.fleet-page .button--big,
.about-page .button--big {
  text-shadow: 1px 1px 1px #a0843e;
}

.fleet-page .button--big::before,
.about-page .button--big::before {
  border: 1px solid #be9943;
  background: linear-gradient(#d4aa49, #be9943);
}

.fleet-page .button--big::after,
.about-page .button--big::after {
  border: 1px solid #000000;
  background: linear-gradient(#313132, #000000);
}

.fleet-page .button--big:hover,
.about-page .button--big:hover {
  text-shadow: 1px 1px 1px #000000;
}

.fleet-page .form .button,
.about-page .form .button {
  text-shadow: 1px 1px 1px #a0843e;
}

.fleet-page .form .button::before,
.about-page .form .button::before {
  border: 1px solid #be9943;
  background: linear-gradient(#d4aa49, #be9943);
}

.fleet-page .form .button::after,
.about-page .form .button::after {
  border: 1px solid #ffffff;
  background: #ffffff;
}

.fleet-page .form .button:hover,
.about-page .form .button:hover {
  color: #000000;
  text-shadow: none;
}

.navigation {
  background: #d4aa49;
